<template>
  <main class="main container">
    <div class="my-5 row justify-content-center">
      <div class="mb-5" v-if="order.is_paid">
        <div class="text-center mb-2">
          <font-awesome-icon :icon="['far', 'check-circle']" class="text-success text-center" size="6x"/>
        </div>
        <p class="h5 font-weight-bold">謝謝你，你的訂單已經成立</p>
      </div>
      <h2 class="text-center h3 col-12 mb-4">訂單資訊</h2>
      <form class="col-md-6" @submit.prevent="payOrder" v-if="order.products">
        <table class="table">
          <thead>
            <th>商名名稱</th>
            <th>期限</th>
            <th class="text-right">價格</th>
          </thead>
          <tbody>
            <tr v-for="item in order.products" :key="item.id">
              <td class="align-middle">{{ item.product.title }}</td>
              <td class="align-middle">{{ item.qty }} {{ item.product.unit }}</td>
              <td class="align-middle text-right">{{ item.final_total | currency }}</td>
            </tr>
          </tbody>
          <tfoot>
            <tr>
              <td class="font-weight-bold">總計</td>
              <td class="font-weight-bold text-right" colspan="3">{{ order.total | currency }}</td>
            </tr>
          </tfoot>
        </table>
        <table class="table" v-if="order.user">
          <tbody>
            <tr>
              <th width="120">顧客姓名</th>
              <td>{{ order.user.name }}</td>
            </tr>
            <tr>
              <th>顧客電話</th>
              <td>{{ order.user.tel }}</td>
            </tr>
            <tr>
              <th>顧客 Email</th>
              <td>{{ order.user.email }}</td>
            </tr>
            <tr>
              <th>付款狀態</th>
              <td>
                <span v-if="!order.is_paid" class="text-danger">尚未付款</span>
                <span v-else class="text-success">付款完成</span>
              </td>
            </tr>
          </tbody>
        </table>
        <div class="text-center">
          <button class="btn btn-danger" v-if="!order.is_paid">確認付款去</button>
          <router-link to="/" class="btn btn-info d-inline-block" v-else>回購物首頁</router-link>
        </div>
      </form>
    </div>
  </main>
</template>

<script>
import { mapState } from 'vuex';

export default {
  name: 'ConfirmPayment',
  props: ['orderId'],
  created() {
    this.$store.dispatch('getOrder', this.orderId);
  },
  computed: {
    ...mapState(['order']),
  },
  methods: {
    payOrder() {
      this.$store.dispatch('payOrder', this.orderId);
    },
  },
};
</script>
